<template>
  <div class="cms-list-layout" style="padding-top:6px">
    <el-tabs v-model="activeName" v-if="status" ref="productTab">
      <el-tab-pane label="公众号列表" name="first">
        <list></list>
      </el-tab-pane>
      <el-tab-pane label="小程序列表" name="second">
        <mini-list></mini-list>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import list from './list'
import miniList from './miniList'
export default {
  name: 'productList',
  components: {
    list,
    miniList
  },
  data() {
    return {
      status: true,
      activeName: 'first'
    }
  },
  methods: {},
  activated() {
    if (this.$route.query.type === 'list') {
      this.status = false
      setTimeout(() => {
        this.status = true
      }, 0)
    }
  },
  created() {
    if (this.$route.query.activeStatus === '2') {
      this.activeName = 'second'
    }
  }
}
</script>

<style>
.el-tabs__header {
  margin: 0 0 10px !important;
}
</style>



