<template>
  <div>
    <section class="cms-list-query" v-if="!isAddMass">
      <div class="query-group" >
        <cms-button size="small" type="success" @click="openPublic">授 权</cms-button> 
      </div>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column prop="levelName" label="账号" min-width="400">
        <template slot-scope="scope">
          <div class="product-table">
            <div class="product-img">
              <img :src="scope.row.headImg" :alt="scope.row.wechatName">
            </div>
            <div class="product-desc">
              <div class="product-name">
                {{scope.row.wechatName}}
              </div>
              <div class="product-attr">
                <span v-if="scope.row.wechatType==1">类型：普通订阅号
                  <i class="iconfont" :class="scope.row.verifyStatus==1?'icon-guanbi error':'icon-gou success'"></i>
                </span>
                <span v-if="scope.row.wechatType==2">类型：服务号
                  <i class="iconfont" :class="scope.row.verifyStatus==1?'icon-guanbi error':'icon-gou success'"></i>
                </span>
              </div>
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="wechatType" label="类型" min-width="220" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.wechatType==1">普通订阅号</span>
          <span v-if="scope.row.wechatType==2">服务号</span>
        </template>
      </el-table-column>
      <el-table-column prop="verifyStatus" label="是否认证" min-width="220" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.verifyStatus==1">
            <i class="iconfont icon-tingyong" style="color:#b7b7b7;"></i>
            <span style="margin:0 8px 0 3px">未认证</span>
          </span>
          <span v-if="scope.row.verifyStatus==2">
            <i class="iconfont icon-gou" style="color:#00DA38;"></i>
            <span style="margin:0 8px 0 3px">已认证</span>
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="owerType" label="拥有者" min-width="220" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.owerType==1">平台</span>
          <span v-if="scope.row.owerType==2">商家</span>
        </template>
      </el-table-column>
      <el-table-column label="操作选项" width='320' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='icon-shezhi1' size='small' type='info' @click="$routerLink('/wechatPublic/setting','list',{id:scope.row.id})">设置</cms-button>
          <cms-button icon='icon-sousuo1' size='small' type='info' @click="$routerLink('/wechatPublic/statistics','update',{id:scope.row.id,appId:scope.row.appId})">进入公众号</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>

<script>
import { UserStatisticsPage, UserStatisticsJudge } from '@/api/wechat/index.js'
import list from '@/mixins/list'
var defaultData = {
  wechatName: '',
  page: 1,
  size: 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      baseUrl: process.env.baseUrl,
      isAddMass: ''// 是否已添加公众号
    }
  },
  methods: {
    getJudgeInfo() { // 判断平台是否已经添加公众号
      UserStatisticsJudge().then(res => {
        this.isAddMass = res.data
      })
    },
    openPublic() {
      this.openBox(this.baseUrl + '/weChat/grantAuth', '_blank')
      window.open(this.baseUrl + '/weChat/grantAuth', '_blank')
    },
    openBox(url, name) { // 打开弹窗
      this.$alert(' 请根据授权结果，选择如下操作！', '操作提示', {
        confirmButtonText: '授权成功',
        cancelButtonText: '授权失败，请重试',
        showCancelButton: true,
        showClose: false,
        distinguishCancelAndClose: true
      }).then(() => {
        this.getTableData(UserStatisticsPage, this.queryParams)
        this.getJudgeInfo()
        location.reload()
      }).catch((err) => {
        if (err === 'cancel') {
          this.openPublic(url, name)
        }
      })
    }
  },
  created() {
    this.getTableData(UserStatisticsPage, this.queryParams)
    this.getJudgeInfo()
  }
}
</script>

<style scoped>
.btn-a {
  background-color: rgb(24, 138, 226);
  width: 50px;
  height: 28px;
  color: #ffffff;
  border-radius: 5px;
  text-align: center;
  line-height: 28px;
  font-size: 14px;
  display: block;
}
.cms-list-query {
  width: 100%;
  padding: 0px 0 0px 0 !important;
  overflow: hidden;
}
.el-tabs__header {
  padding: 0;
  position: relative;
  margin: 0 0 10px !important;
}
</style>




